import React from 'react';
import SubmitButton from '../../../view-utils/SubmitButton';
import { Container, Center } from '../../../view-utils/Framework';
import styled from 'styled-components'; 
import { withRouter } from 'react-router-dom'; 

const HeaderStyled = styled.header`
  text-align: center;
  font-size: 1.7em;
`
const Wrapper = styled.div`
  text-align: center;
  
`
  const GeneralSection = props => {
  const { history } = props;

 
  const OnSubmit = (event) => {
     console.log(props);
     
    let id = props.match.params.id;
    let username = props.user.username;
    history.push(`/questions/${username}/${id}`)
    

  }
   
  let self = props.category;
 let user = props.user;
 
  return (
    <Wrapper>
      <HeaderStyled>  Hi {user.username} , You have Selected, { self.title } </HeaderStyled>
      <Container>
        <Center>
          <div className='start-page'>
            <SubmitButton className='start-button' id='startQuizButton'  type="button"  onClick={OnSubmit}>
              Start Your Quiz!
        </SubmitButton>
          </div>
        </Center>
      </Container>

    </Wrapper>
  )

}

export default withRouter(GeneralSection);
